import React, {Component, lazy, Suspense} from 'react';
import {
	Badge,
	Card,
	CardBody,
	CardFooter,
	CardHeader,
	CardTitle,
	List,
	ListGroup,
	ListGroupItem,
	ListGroupItemHeading,
	ListGroupItemText,
	InputGroup,
	InputGroupAddon,
	Collapse,
	Form,
	FormGroup,
	Label,
	Modal,
	ModalHeader,
	ModalBody,
	ModalFooter,
	Input,
	Fade,
	Col,
	Row,
	Button
} from 'reactstrap';
import Widget04 from '../../views/Widgets/Widget04';
import {CustomTooltips} from '@coreui/coreui-plugin-chartjs-custom-tooltips';
import {getStyle, hexToRgba} from '@coreui/coreui/dist/js/coreui-utilities';
import axios from 'axios';

const Loading = () => <div>Loading...</div>;

class Friends extends Component {
	constructor(props) {
		super(props);
		this.state = {
			userID: sessionStorage.getItem("user_id"),
			friends: [
				{
					uid: 1,
					uname: "Mengyu Han",
					connected_time: "2018\/10\/9 19:04:11"
				},
				{
					uid: 2,
					uname: "Zhufeng Xu",
					connected_time: "2018\/10\/9 19:04:11"
				}
			],
			modal: false,
			success: false,
			warning: false,
			info: false,
			searchSuccess: false
		};
		this.updateFriends = this.updateFriends.bind(this);
		this.deleteFriend = this.deleteFriend.bind(this);
		this.addFriend = this.addFriend.bind(this);
		this.toggle = this.toggle.bind(this);
		this.toggleSuccess = this.toggleSuccess.bind(this);
		this.toggleSearchSuccess = this.toggleSearchSuccess.bind(this);
		this.toggleWarning = this.toggleWarning.bind(this);
		this.toggleInfo = this.toggleInfo.bind(this);

		this.updateFriends();
	}

	updateFriends() {
		axios.get('http://localhost:8080/api/friend/' + this.state.userID)
			.then(response => {
				this.setState({
					friends: response.data
				});
			}, error => {
				console.log(error);
			});
	}

	//add comment alert
	toggle() {
		this.setState({
			modal: !this.state.modal,
		});
	}

	toggleSuccess() {
		this.setState({
			success: !this.state.success,
		});
	}

	toggleWarning() {
		this.setState({
			warning: !this.state.warning,
		});
	}

	toggleInfo() {
		if (this.state.userID == undefined) {
			window.alert("You have not login yet, please login first!");
		} else {
			this.setState({
				info: !this.state.info,
			});
		}
	}

	toggleSearchSuccess() {
		this.setState({
			searchSuccess: !this.state.searchSuccess,
		});
	}

	deleteFriend(uid) {
		if (this.state.userID == undefined) {
			window.alert("You have not login yet, please login first!");
		} else {
			axios.delete('http://localhost:8080/api/friend/' + this.state.userID, {
				data: {
					uid_friend: uid
				}
			})
				.then(response => {
						this.toggleSuccess();
						this.updateFriends();
					},
					error => {
						this.toggleWarning();
					});
		}
	}

	addFriend() {
		var searchUserName = document.getElementById("searchUserName").value;
		//http request search for the user
		// if user not exist, warn not exits
		// if user exist, add directly

		axios.post('http://localhost:8080/api/friend/' + this.state.userID, {
			uname_friend: searchUserName
		})
			.then(response => {
					if (response.data) {
						this.toggleInfo();
						this.toggleSearchSuccess();
						this.updateFriends();
					} else {
						window.alert("Not valid user");
					}
				},
				error => {
					this.toggleWarning();
				});
	}

	render() {
		return (
			<div className="animated fadeIn normal-wrapper">
				<h1>Friends</h1>
				<Row>
					{this.state.friends.map((object, i) => {
						return <Col key={i} xs="12" sm="6" lg="3">
							<div>
								<Widget04 icon="icon-people" color="info"
										  header={object.uname}>{"Connect time:" + object.connected_time}<br></br><br></br>
									<Button className="d-block btn-small btn-round float-right btn-outline-danger"
											onClick={this.deleteFriend.bind(this, object.uid)}>Delete</Button>
								</Widget04>
							</div>
						</Col>;

					})}
					<Col xs="12" sm="6" lg="3">
						<Widget04 onClick={this.toggleInfo} icon="icon-user-follow text-white" className="bg-primary"
								  color="info"
								  style={{cursor: "pointer"}}
								  header={"Add a Friend"}><br/><br/><br/>
						</Widget04>
					</Col>

					<Modal isOpen={this.state.success} toggle={this.toggleSuccess}
						   className={'modal-success ' + this.props.className}>
						<ModalHeader toggle={this.toggleSuccess}>Delete Success</ModalHeader>
						<ModalBody>
							You will not see the user in your user list any more.
						</ModalBody>
						<ModalFooter>
							<Button color="success" onClick={this.toggleSuccess}>OK</Button>{' '}
						</ModalFooter>
					</Modal>
					<Modal isOpen={this.state.info} toggle={this.toggleInfo}
						   className={'modal-info ' + this.props.className}>
						<ModalHeader toggle={this.toggleInfo}>Search User</ModalHeader>
						<ModalBody>
							<FormGroup>
								<Label htmlFor="ccmonth">Please enter user name</Label>
								<InputGroup>
									<InputGroupAddon addonType="prepend">
										<Button type="button" color="primary" onClick={this.addFriend}>
											<i className="fa fa-search"/> Search</Button>
									</InputGroupAddon>
									<Input type="text" id="searchUserName" placeholder="Username"/>
								</InputGroup>
							</FormGroup>
						</ModalBody>
						<ModalFooter>
							<Button color="secondary" onClick={this.toggleInfo}>Cancel</Button>
						</ModalFooter>
					</Modal>
					<Modal isOpen={this.state.warning} toggle={this.toggleWarning}
						   className={'modal-warning ' + this.props.className}>
						<ModalHeader toggle={this.toggleWarning}>Duplicate Operation</ModalHeader>
						<ModalBody>
							This user has already been your friend.
						</ModalBody>
						<ModalFooter>
							<Button color="warning" onClick={this.toggleWarning}>OK</Button>{' '}
							<Button color="secondary" onClick={this.toggleWarning}>Cancel</Button>
						</ModalFooter>
					</Modal>
					<Modal isOpen={this.state.searchSuccess} toggle={this.toggleSearchSuccess}
						   className={'modal-success ' + this.props.className}>
						<ModalHeader toggle={this.toggleSearchSuccess}>Delete Success</ModalHeader>
						<ModalBody>
							You have successfully add the user to your friend list.
						</ModalBody>
						<ModalFooter>
							<Button color="success" onClick={this.toggleSearchSuccess}>OK</Button>{' '}
						</ModalFooter>
					</Modal>
				</Row>
			</div>
		);
	}
}

export default Friends;
